.navcon{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: rgb(218, 216, 216);
    font-size: 15px;
    
}
.navcon link{
    color:azure;
     font-size: 15px;
    

}
.card{
    width:250px;
    height:500px;
    color:rgb(246, 246, 252);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    border: 2px solid grey;
    border-radius: 5%;
    margin-bottom:20px;
    padding: 30px;
    transition: width 2s;
    box-shadow: 0px 0px 10px rgb(237, 20, 4);
    background:linear-gradient( to right bottom,rgb(9, 9, 9),rgb(44, 44, 44),rgba(6, 6, 6, 0.995));
}
.card:hover{
    /* width:300px; */
    
    background:linear-gradient( to left bottom,rgb(12, 12, 12),rgb(25, 2, 139),rgb(8, 8, 8),rgb(12, 12, 12),rgb(25, 2, 139),rgb(8, 8, 8));
    transform: scaleX(110%);
    color:linear-gradient( to right bottom,rgb(9, 9, 9),rgb(44, 44, 44),rgba(6, 6, 6, 0.995));
   
    box-shadow: 0px 0px 15px rgb(240, 238, 238);
}
.textcon{
    width:100%;
    display:block;
    align-items: center;
    
}
.gridCont{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr  ;
    margin:40px;
    padding-left:60px;
    /* border: 2px solid green; */
}
#logo{
    width:35%;
    height:95%;
    border-radius: 25px;
    background: transparent;
}
.cardinner{
    align-items: center;
}

.imginner{
    margin: auto;
    width:100%;
    height: 30vh;
   
   
}

#img{
    
    width:100%;
    height: 100%;
    border-radius: 15px;
}
.wellcm{
    color:azure;
    transition: 10s;
    transform: matrix3d(360);
    text-shadow:0px 0px 10px red ;
    font-size:25px ;
   
}
.footer{
    width:100%;
    color: azure;
    background:linear-gradient( to  bottom,rgb(14, 14, 14),rgb(70, 69, 69),rgb(9, 9, 9));
}
.footertext{
    display:flex;
    justify-content: space-around;
    color:azure;
    background:linear-gradient( to right bottom,rgb(9, 9, 9),rgb(67, 66, 66),rgb(5, 5, 5));
}
